app-shrinking-header {
  width: 100%;
  padding-left: 15%;
  padding-right: 15%;
  z-index: 0;
  position: fixed;
  font-size: 130%;
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  h1 {
    margin-top: 0px;
    font-size: inherit;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
